<template>
	<div>
		<Header></Header>
		<SearchInput @onSearchUserInput="onSearchUser"></SearchInput>
		<div class="container profile" v-if="isInputValue">
			<div class="card p-2" style="margin-top: 50px;">
				<div class="text-center">
					<img :src="user.avatar_url" class="rounded git_avatar_url"/>
				</div>
				<div>
					<h2 class="text-white text-center git_name"></h2>
					<p class="text-white text-center git_public_repos">Name: {{user.login}}</p>
					<p class="text-white text-center git_followers">Repositories: {{user.public_repos}}</p>
					<p class="text-white text-center git_blog">Followers: {{user.followers}}</p>
					<p class="text-white git_bio">Bio: {{user.bio}}</p>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
import Header from './components/Header.vue';
import SearchInput from './components/SearchInput.vue';
import axios from 'axios';

export default {
	data(){
		return {
			user: {},
			isInputValue: false
		}
	},
	components: {
		Header,
		SearchInput
	},
	methods: {
		onSearchUser(userInput){
			this.isInputValue = true;
			axios.get(`https://api.github.com/users/${userInput}`)
				.then(resp => { 
					this.user = resp.data;
				})
				.catch(err => {
					if(err.response){
						throw new Error(err.message);
					}else if(err.request){
						throw new Error(err.message);
					}else {
						throw new Error(err.message);
					}
				})
		}
	}
}
</script>