<template>
    <div class="container-form mt-100">
        <input type="search" v-model="userInput" class="form-control mr-10" placeholder="Search for a new github profile">
        <button class="btn default" @click="onUserAction">Search New Profile</button> 
    </div>
</template>

<script>
export default {
    data(){
        return {
            userInput: ''
        }
    },
    methods: {
        onUserAction(){
            this.$emit('onSearchUserInput', this.userInput);
            this.userInput = '';
        },
    },
    created(){
        const self = this;
        window.addEventListener('keydown', (e) => {
            if(e.key === 'Enter'){
                this.$emit('onSearchUserInput', self.userInput);
                self.userInput = '';
            }
        })
    }
}
</script>